* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
    background-color: rgba(0, 0, 0, 0.8);
}

html, body {
    height: 100%;
}


.home {
  position: absolute;
  top: 50%;
  left: 0 ;
  right: 0;
  margin: -70px auto 0 auto;
}

h1, h2 {
    font-family: "Helvetica", Helvetica, Arial, sans-serif;
    font-weight: 200;
    color: rgba(255,250,244,1);
    text-align: center;
    font-size: 14px;
    letter-spacing: 3px;
    padding: 2px 0;
    margin: 0;
}

.mug {
    width: 80px;
    height: 120px;
    position: relative;
    opacity: 1;
    margin: 30px auto;
}

.handle {
    width: 20px;
    height: 55px;
    position: absolute;
    top: 20px;
    left: 105%;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border-top: 12px solid rgba(225,235,240,1);
    border-right: 12px solid rgba(225,235,240,1);
    border-bottom: 12px solid rgba(225,235,240,1);
}

.mug .glass {
    opacity: 0.8;
}

.mug .body {
    width: 100%;
    height: 100%;
    background-color: rgba(225,235,240,1);
    border: 5px solid transparent;
    position: absolute;
    left: 0;
    bottom: 0;
    margin-left: -5px;
    margin-bottom: -5px;
    overflow: hidden;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
}

.mug .contents {
    width: 100%;
    height: 80%;
    background-color: rgba(241,142,28,0.8);
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.mug .contents .fizz {
    position: absolute;
    margin-top: -45px;
    margin-left: 6px;
}

.mug .contents .fizz div {
    width: 4px;
    height: 4px;
    border-radius: 4px;
    position: absolute;
    margin-top: 130px;
    border: 2px solid rgba(255,255,255,1);
    opacity: 0;
}

.mug .contents .fizz div:nth-of-type(1){
    margin-left: 4px;
    animation: bubblebubble 2.5s linear 0.5s infinite normal forwards;
	-webkit-animation: bubblebubble 2.5s linear 0.5s infinite normal forwards;
}

.mug .contents .fizz div:nth-of-type(2){
    margin-left: 13px;
    animation: bubblebubble 2.45s linear 0s infinite normal forwards;
	-webkit-animation: bubblebubble 2.45s linear 0s infinite normal forwards;
}

.mug .contents .fizz div:nth-of-type(3){
    margin-left: 22px;
    animation: bubblebubble 2.35s linear 0.8s infinite normal forwards;
	-webkit-animation: bubblebubble 2.35s linear 0.8s infinite normal forwards;
}

.mug .contents .fizz div:nth-of-type(4){
    margin-left: 30px;
    animation: bubblebubble 2.45s linear 0.5s infinite normal forwards;
	-webkit-animation: bubblebubble 2.45s linear 0.5s infinite normal forwards;
}

.mug .contents .fizz div:nth-of-type(5){
    margin-left: 39px;
    animation: bubblebubble 2.5s linear 2s infinite normal forwards;
	-webkit-animation: bubblebubble 2.5s linear 2s infinite normal forwards;
}

.mug .contents .fizz div:nth-of-type(6){
    margin-left: 47px;
    animation: bubblebubble 2.6s linear 0.5s infinite normal forwards;
	-webkit-animation: bubblebubble 2.6s linear 0.5s infinite normal forwards;
}

.mug .contents .fizz div:nth-of-type(7){
    margin-left: 55px;
    animation: bubblebubble 2.5s linear 1.5s infinite normal forwards;
	-webkit-animation: bubblebubble 2.5s linear 1.5s infinite normal forwards;
}

.mug .contents .foam {
    width: 100%;
    height: 35px;
    overflow: hidden;
    position: absolute;
    top: 0;
    margin-top: -35px;
    left: 0;
    opacity: 0.65;
}

.mug .contents .foam div {
    background-color: rgba(241,206,152,1 );
    position: absolute;
    border-radius: 25px;
    width: 30px;
    height: 30px;
}

.mug .contents .foam div:nth-of-type(even){
    left: -15px;
    top: 10px;
    width: 40px;
    height: 40px;
}

.mug .contents .foam div:nth-of-type(odd){
    left: -5px;
    top: 0;
}

.mug .contents .foam div:nth-of-type(5n+1){
    margin-left: 0%;
    margin-top: 15px;
    animation: bubble 6.5s ease-in-out 0.2s infinite alternate none;
	-webkit-animation: bubble 6.5s ease-in-out 0.2s infinite alternate none;
}

.mug .contents .foam div:nth-of-type(5n+2){
    margin-left: 25%;
    margin-top: 15px;
    animation: bubble 6.2s ease-in-out 0.1s infinite alternate none;
	-webkit-animation: bubble 6.2s ease-in-out 0.1s infinite alternate none;
}

.mug .contents .foam div:nth-of-type(5n+3){
    margin-left: 50%;
    margin-top: 15px;
    animation: bubble 6.5s ease-in-out 0s infinite alternate none;
	-webkit-animation: bubble 6.5s ease-in-out 0s infinite alternate none;
}

.mug .contents .foam div:nth-of-type(5n+4){
    margin-left: 75%;
    margin-top: 15px;
    animation: bubble 6.6s ease-in-out 0s infinite alternate none;
	-webkit-animation: bubble 6.6s ease-in-out 0s infinite alternate none;
}

.mug .contents .foam div:nth-of-type(5n+5){
    margin-left: 100%;
    margin-top: 15px;
    animation: bubble 6.15s ease-in-out 0.6s infinite alternate none;
	-webkit-animation: bubble 6.15s ease-in-out 0.6s infinite alternate none;
}

@keyframes bubble {
	100% { -webkit-transform: translate(-3px , 4px ); -ms-transform: translate(-3px , 4px ); transform: translate(-3px , 4px );}
	75% { -webkit-transform: translate(5px , -2px ); -ms-transform: translate(5px , -2px ); transform: translate(5px , -2px );}
	50% { -webkit-transform: translate(-3px , 1px ); -ms-transform: translate(-3px , 1px ); transform: translate(-3px , 1px );}
	25% { -webkit-transform: translate(3px , 4px ); -ms-transform: translate(3px , 4px ); transform: translate(3px , 4px );}
	0% { -webkit-transform: translate(-5px , -2px ); -ms-transform: translate(-5px , -2px ); transform: translate(-5px , -2px );}
}

@-webkit-keyframes bubble {
	100% { -webkit-transform: translate(-3px , 4px ); -ms-transform: translate(-3px , 4px ); transform: translate(-3px , 4px );}
	75% { -webkit-transform: translate(5px , -2px ); -ms-transform: translate(5px , -2px ); transform: translate(5px , -2px );}
	50% { -webkit-transform: translate(-3px , 1px ); -ms-transform: translate(-3px , 1px ); transform: translate(-3px , 1px );}
	25% { -webkit-transform: translate(3px , 4px ); -ms-transform: translate(3px , 4px ); transform: translate(3px , 4px );}
	0% { -webkit-transform: translate(-5px , -2px ); -ms-transform: translate(-5px , -2px ); transform: translate(-5px , -2px );}
}

@keyframes bubblebubble {
	50% { -webkit-transform: translate(0px , -130px ); -ms-transform: translate(0px , -130px ); transform: translate(0px , -130px ); opacity: 0;}
	40% { -webkit-transform: translate(3px , -90px ); -ms-transform: translate(3px , -90px ); transform: translate(3px , -90px ); opacity: 0.4;}
	30% { -webkit-transform: translate(-3px , -60px ); -ms-transform: translate(-3px , -60px ); transform: translate(-3px , -60px ); opacity: 0.6;}
	100% { opacity: 0; -webkit-transform: translate(0px , 0px ); -ms-transform: translate(0px , 0px ); transform: translate(0px , 0px );}
	90% { -webkit-transform: translate(0px , -130px ); -ms-transform: translate(0px , -130px ); transform: translate(0px , -130px ); opacity: 0;}
	20% { -webkit-transform: translate(4px , -40px ); -ms-transform: translate(4px , -40px ); transform: translate(4px , -40px ); opacity: 0.7;}
	10% { -webkit-transform: translate(-4px , -20px ); -ms-transform: translate(-4px , -20px ); transform: translate(-4px , -20px ); opacity: 0.8;}
	0% { -webkit-transform: translate(0px , 0px ); -ms-transform: translate(0px , 0px ); transform: translate(0px , 0px ); opacity: 0.9;}
}

@-webkit-keyframes bubblebubble {
	50% { -webkit-transform: translate(0px , -130px ); -ms-transform: translate(0px , -130px ); transform: translate(0px , -130px ); opacity: 0;}
	40% { -webkit-transform: translate(3px , -90px ); -ms-transform: translate(3px , -90px ); transform: translate(3px , -90px ); opacity: 0.4;}
	30% { -webkit-transform: translate(-3px , -60px ); -ms-transform: translate(-3px , -60px ); transform: translate(-3px , -60px ); opacity: 0.6;}
	100% { opacity: 0; -webkit-transform: translate(0px , 0px ); -ms-transform: translate(0px , 0px ); transform: translate(0px , 0px );}
	90% { -webkit-transform: translate(0px , -130px ); -ms-transform: translate(0px , -130px ); transform: translate(0px , -130px ); opacity: 0;}
	20% { -webkit-transform: translate(4px , -40px ); -ms-transform: translate(4px , -40px ); transform: translate(4px , -40px ); opacity: 0.7;}
	10% { -webkit-transform: translate(-4px , -20px ); -ms-transform: translate(-4px , -20px ); transform: translate(-4px , -20px ); opacity: 0.8;}
	0% { -webkit-transform: translate(0px , 0px ); -ms-transform: translate(0px , 0px ); transform: translate(0px , 0px ); opacity: 0.9;}
}


